const app = getApp();
export default {
	data() {
		return {
			list: {}
		};
	},
	onLoad(e) {
		console.log(e);
		this.id = e.id;
		this.getList();
	},
	methods: {
		getList() {
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}details/getExchangeCode`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					curricula_id: this.id
				},
				success: res => {
					uni.hideLoading();
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.list = res.data;
					} else {
						this.list = [];
					}
				}
			});
		},
		go(e) {
			uni.setStorageSync('coupon', e);
			uni.navigateBack({
				delta: 1
			});
		}
	}
};
